import { css } from 'lit'

const greenThemeColor = {
  light: {
    inverse: `rgb(255,255,255)`,
    foreground: `rgb(0, 196, 84)`,
    background: `rgb(218,246,218)`
  }
}

function themeColors() {
  return {
    default: greenThemeColor
  }
}

function themeModeColors() {
  return {
    light: {
      foreground: {
        1: `rgb(20,20,20)`,
        2: `rgb(121,134,134)`,
        3: `rgb(158,169,169)`
      },
      background: {
        1: `rgb(255,255,255)`,
        2: `rgb(241,243,243)`,
        3: `rgb(228,231,231)`
      },
      overlay: 'rgba(0,0,0,0.1)'
    }
  }
}

export const ThemeUtil = {
  color() {
    const themeColor = 'default'
    const themeMode = 'light'
    const accent = themeColors()[themeColor][themeMode]
    const theme = themeModeColors()[themeMode]

    return {
      foreground: {
        accent: accent.foreground,
        inverse: accent.inverse,
        ...theme.foreground
      },
      background: {
        accent: accent.background,
        ...theme.background
      },
      overlay: theme.overlay,
      error: `rgb(242, 90, 103)`,
      success: `rgb(0, 196, 84)`
    }
  },

  setTheme() {
    const root: HTMLElement | null = document.querySelector(':root')

    if (root) {
      const variables = {
        '--debox-color-fg-accent': ThemeUtil.color().foreground.accent,
        '--debox-color-fg-inverse': ThemeUtil.color().foreground.inverse,
        '--debox-color-fg-1': ThemeUtil.color().foreground[1],
        '--debox-color-fg-2': ThemeUtil.color().foreground[2],
        '--debox-color-fg-3': ThemeUtil.color().foreground[3],
        '--debox-color-bg-1': ThemeUtil.color().background[1],
        '--debox-color-bg-2': ThemeUtil.color().background[2],
        '--debox-color-bg-3': ThemeUtil.color().background[3],
        '--debox-color-overlay': ThemeUtil.color().overlay,
        '--debox-color-err': ThemeUtil.color().error,
        '--debox-color-success': ThemeUtil.color().success
      }
      Object.entries(variables).forEach(([key, val]) => root.style.setProperty(key, val))
    }
  },

  globalCss: css`
    *,
    *::after,
    *::before {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      font-style: normal;
      text-rendering: optimizeSpeed;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      -webkit-tap-highlight-color: transparent;
      backface-visibility: hidden;
    }

    button {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      border: none;
      background-color: transparent;
    }

    button::after {
      content: '';
      position: absolute;
      inset: 0;
      transition: background-color, 0.2s ease;
    }

    button:disabled {
      cursor: not-allowed;
    }

    button svg,
    button debox-text {
      position: relative;
      z-index: 1;
    }

    input {
      border: none;
      outline: none;
      appearance: none;
    }

    img {
      display: block;
    }

    ::selection {
      color: var(--debox-color-fg-inverse);
      background: var(--debox-color-fg-accent);
    }

    .ellipsis {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .clearfix {
      *zoom: 1;
    }
    .clearfix::before,
    .clearfix::after {
      display: table;
      clear: both;
      content: ' ';
    }
    .clearfix::after {
      display: block;
      height: 1px;
      margin-top: -1px;
      visibility: hidden;
    }

    .container {
      border-radius: 21px;
      background-color: white;
      overflow: hidden;
    }

    .cal_bottom::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }
    .cal_bottom::-webkit-scrollbar-thumb {
      border-radius: 5px;
      background: var(--debox-color-bg-3);
    }

    .cal_bottom::-webkit-scrollbar-track {
      border: 0;
    }

    .cal_bottom::-webkit-scrollbar-button {
      width: 0;
      height: 0;
      display: none;
    }

    .cal_bottom::-webkit-scrollbar-corner {
      background-color: var(--debox-color-fg-1);
    }

    .hover {
      transition: all 300ms;
    }
    .hover:hover {
      cursor: pointer;
      opacity: 0.8;
    }

    .center {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  `
}
