/* eslint-disable @typescript-eslint/no-non-null-assertion */
/* eslint-disable @typescript-eslint/restrict-plus-operands */
import { html, LitElement, type PropertyValues } from 'lit'
import { customElement, property, state, query } from 'lit/decorators.js'
import { classMap } from 'lit/directives/class-map.js'
import interact from 'interactjs'

import { ThemeUtil } from '../../utils/ThemeUtil'
import { SvgUtil } from '../../utils/SvgUtil'
import styles from './styles.css'

interface TJson {
  domId?: string
  type: string
  functionName?: string
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  data?: any
}

enum EMessageType {
  // from iframe
  CONVERSATION_STATUS = 'CONVERSATION_STATUS',
  IM_MESSAGE = 'IM_MESSAGE'
}

// eslint-disable-next-line no-underscore-dangle
const __IFRAME_URL_ORIGIN__ = new URL(__IFRAME_URL__).origin

@customElement('debox-chat-widget')
export class DeBoxChat extends LitElement {
  public static styles = [ThemeUtil.globalCss, styles]

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private dragAction: any = null
  private startX = 0
  private startY = 0
  private isDragging = false
  private readonly dragThreshold = 5

  @property()
  public projectId?: string

  @property()
  public conversationId?: string

  @state()
  private open = false

  // 这个是表示完成过一次通信, 后面应该会改这个东西，有点问题
  @state()
  private iframeLoaded = false

  @state()
  private iframeUrl = ''

  @query('#sdk-container')
  private readonly sdkContainer!: HTMLDivElement

  // @query('#sdk-iframe')
  // private readonly sdkIframe?: HTMLIFrameElement

  private readonly domId = `sdk-dom-${Math.random().toString(36).substring(2, 9)}`

  public constructor() {
    super()
    ThemeUtil.setTheme()
  }

  public connectedCallback() {
    super.connectedCallback()

    window.addEventListener('message', this.getIframeMsgFn)
  }

  public disconnectedCallback() {
    super.disconnectedCallback()

    window.removeEventListener('message', this.getIframeMsgFn)
  }

  public firstUpdated() {
    this.enableDrag()
  }

  public updated(changedProperties: PropertyValues<this>) {
    if (changedProperties.has('conversationId') || changedProperties.has('projectId')) {
      // console.log('updated conversationId:', this.conversationId, 'projectId:', this.projectId)

      this.iframeLoaded = false
    }
  }

  // --------------iframe start----------------
  // private readonly postIframeMsgFn = (data: TJson) => {
  //   if (!this.iframeLoaded || !this.sdkIframe) {
  //     return
  //   }

  //   console.log('postToIframeMsgFn:', data)

  //   try {
  //     this.sdkIframe.contentWindow?.postMessage(data, __IFRAME_URL_ORIGIN__)
  //   } catch (e) {
  //     console.error(e)
  //   }
  // }

  private readonly getIframeMsgFn = (event: MessageEvent) => {
    if (!event.origin || ![__IFRAME_URL_ORIGIN__].includes(event.origin)) {
      return
    }

    const json: TJson = typeof event.data === 'object' ? event.data : { domId: '' }

    // console.log('getFromIframeMsgFn:', json)

    if (json.domId === this.domId) {
      this.iframeLoaded = true

      switch (json.type) {
        case EMessageType.CONVERSATION_STATUS:
          // 判断当前连接的会话是否一致，不一致则发送会话变更，以npm会话为准
          if (
            (this.conversationId || '') !== (json.data?.conversationId || '') ||
            (this.projectId || '') !== (json.data?.projectId || '')
          ) {
            // 直接忽略
          } else {
            // 转发给三方
            this.pushEventMessage({
              type: EMessageType.CONVERSATION_STATUS,
              data: json.data
            })
          }
          break

        // 后续弹幕使用
        // case EMessageType.IM_MESSAGE:
        //   break

        default:
          break
      }
    }
  }

  // --------------dragging start----------------
  private closeFn() {
    this.toggleChat()

    this.iframeUrl = ''
    this.iframeLoaded = false
  }

  private toggleChat() {
    this.open = !this.open

    if (this.open && !this.iframeUrl) {
      this.iframeUrl = __IFRAME_URL__
    }

    setTimeout(() => {
      this.dragAction.reflow({
        name: 'drag',
        edges: { left: true, bottom: true }
      })
    }, 500)
  }

  private enableDrag() {
    this.dragAction = interact(this.sdkContainer).draggable({
      listeners: {
        move: event => {
          // 当屏幕小于600px的时候，并且是open状态下，禁止拖动
          if (window.innerWidth < 600 && this.open) {
            return
          }

          const target = event.target as HTMLElement
          const x = (parseFloat(target.getAttribute('data-x')!) || 0) + event.dx
          const y = (parseFloat(target.getAttribute('data-y')!) || 0) + event.dy

          target.style.transform = `translate(${x}px, ${y}px)`
          target.setAttribute('data-x', x.toString())
          target.setAttribute('data-y', y.toString())
        }
      },
      modifiers: [
        interact.modifiers.restrictRect({
          restriction: 'parent',
          endOnly: true
        })
      ],
      inertia: true
    })
  }

  // --------------dragging click start----------------
  private handlePointerDown(event: PointerEvent) {
    this.startX = event.clientX
    this.startY = event.clientY
    this.isDragging = false
  }

  private handlePointerMove(event: PointerEvent) {
    const deltaX = Math.abs(event.clientX - this.startX)
    const deltaY = Math.abs(event.clientY - this.startY)

    if (deltaX > this.dragThreshold || deltaY > this.dragThreshold) {
      this.isDragging = true
    }
  }

  private handlePointerUp() {
    if (!this.isDragging) {
      this.toggleChat()
    }

    this.isDragging = false
  }
  // --------------dragging click end----------------

  // --------------push msg start----------------

  private pushEventMessage(newMessage: { type: EMessageType; data: Record<string, unknown> }) {
    const event = new CustomEvent('debox-message-pushed', {
      detail: newMessage,
      bubbles: true,
      composed: true
    })

    this.dispatchEvent(event)
  }

  // --------------push msg end----------------

  protected render() {
    return html`
      <div
        id="sdk-container"
        class="${classMap({
          container: true,
          open: this.open
        })}"
      >
        <div
          id="sdk-bubble"
          class="bubble"
          @pointerdown="${this.handlePointerDown}"
          @pointermove="${this.handlePointerMove}"
          @pointerup="${this.handlePointerUp}"
        >
          ${SvgUtil.BUBBLE}
        </div>
        <div class="main">
          <div class="toolbar" id="sdk-toolbar">
            <div class="title">DeBox</div>
            <div class="actions">
              <div class="btn close" title="Close" @click="${this.closeFn}">${SvgUtil.CLOSE}</div>
              <div class="btn minimize" title="Minimize" @click="${this.toggleChat}">
                ${SvgUtil.MINIMIZE}
              </div>
            </div>
          </div>
          <div class="iframe-container">
            ${this.iframeLoaded
              ? null
              : html`<div class="loading"><debox-spinner></debox-spinner></div>`}
            ${this.iframeUrl
              ? html`<iframe
                  id="sdk-iframe"
                  src="${this.iframeUrl}?webDomId=${this.domId}&projectId=${this
                    .projectId}&conversationId=${this.conversationId}"
                ></iframe>`
              : null}
          </div>
        </div>
      </div>
    `
  }
}
export default DeBoxChat

declare global {
  interface HTMLElementTagNameMap {
    'debox-chat-widget': DeBoxChat
  }
}
